              :to="item.url" class="items" active-class="active">
              <a>{{ item.text }}</a>
            </router-link> -->

            <li class="btn">
              <a href="#"><i class="fas fa-bars"></i></a>
            </li>
          </ul>

          <!-- <img :src="photo" alt="" class="photo"> -->
        </nav>
      </font>
    </div>
  </div>
</template>


<script>

import { mapActions, mapGetters } from 'vuex';

export default {
  name: "Header",
  data() {
    return {
      msg: "",
    };
  },
  computed: {
    ...mapGetters('menu', { menuList: 'items' }),
    // ...mapGetters(['nickname', 'photo'])
  },
  // methods: {
  //   ...mapActions(['getUserData'])
