<style lang="scss">
$bp-largest: 900px; // 1200px = 75em

.photo {
  position: fixed;
  right: 2.2vh;
  top: 1.2vh;

  height: 3.5vh;
  width: 3.5vh;

  border-radius: 100px;
  border: 2px solid rgb(128, 128, 128);
