}

.headerinside {
  height: 100%;
  background: #151515;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Arial, Helvetica, sans-serif;
}

.logo_img {
  height: 4vh;
}

nav {
  background: #151515;
  padding: 1vh 4rem;
}

nav ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;

  @media only screen and (max-width: $bp-largest) {
    display: none;
  }
}

nav ul li {
  // padding: 1rem 0;
  cursor: pointer;
}

nav ul li.items {
  position: relative;
  width: auto;
  margin: 0 1.2rem;
  text-align: center;
  order: 3;
}

// nav ul li.items:after {
//   position: absolute;
//   content: '';
//   left: 0;
//   bottom: 0.5rem;
//   height: 0.2rem;
//   width: 100%;
//   background: #33ffff;
//   opacity: 0;
//   transition: all 0.2s linear;
// }

nav ul li.items:hover:after {
  opacity: 1;
  bottom: 0.8rem;
}

nav ul li.logo {
  flex: 1;
  color: white;
  font-size: 3vh;
