}

nav ul li a {
  color: white;
  font-size: 18px;
  text-decoration: none;
  transition: .4s;
}

nav ul li:hover a {
  color: cyan;
}

nav ul li i {
  font-size: 20px;
}

nav ul li.btn {
  display: none;
}
nav ul li.btn.hide i:before {
